export default function MainCtrl() {
  var vm = this;
  // 
  vm.routes_p = [
    {
      url: 'jlbj',
      text: '',
      active: false
    },
    {
      url: 'jlyl',
      text: '',
      active: false
    },
    {
      url: 'ytjl',
      text: '',
      active: false
    },
    {
      url: 'zwsc',
      text: '',
      active: false
    },
    {
      url: 'preplay',
      text: '',
      active: false
    }
  ];
  // 
  vm.routes_c = [
    {
      url: 'fbzp',
      text: '',
      active: false
    },
    {
      url: 'zplb.published',
      text: '',
      active: false
    },
    {
      url: 'jllb.filter',
      text: '',
      active: false
    },
    {
      url: 'replay',
      text: '',
      active: false
    }
  ];
  let cookies = {};
  document.cookie.split('; ').forEach(item => {
    let arr = item.split('=');
    cookies[arr[0]] = arr[1];
  });
  if (cookies.accountType) {
    vm.accountType = cookies.accountType;
    vm.logintype = cookies.logintype;
  }
  vm.routes = {
    title: '',
    items: vm.logintype == 1 ? vm.routes_p : vm.routes_c
  };
}



