export default function PreplayCtrl($scope) {
  var vm = this;
  vm.list = [
    { name: '', key: [
      { keyname: '', keyword: 'about' },
      { keyname: '[]', keyword: 'huifu' },
    ], width: '50%' },
    { name: '', key: 'company', width: '25%' },
    { name: '', key: [
      { keyname: '', keyword: 'position' },
      { keyname: '', keyword: 'money' }
    ], width: '25%' },
  ];
  $scope.style = {
    top: '-9999px',
    left: '-9999px'
  };
}



