export default function YtjlCtrl($http) {
  var vm = this;
  vm.para = {
    
  };
  vm.list = [
     { name: '', key: 'position', width: '20%' },
     { name: '', key: 'money', width: '20%' },
     { name: '', key: 'company', width: '20%' },
     { name: '', key: 'gzdd', width: '10%' },
     { name: '', key: 'tdzt', width: '10%' },
     { name: '', key: 'qysfck', width: '10%' }
  ];
  vm.isshow = false;
  vm.comment = function (jobid, duid) {
    $http.post('/HRMspj/PjDetail', { jobid: jobid, duid: duid }).success((d) => {
      if (d.success) {
        vm.data = d.result[0];
        vm.isshow = true;
      } else {
        alert('');
      }
    });
  };
  vm.postcomment = function () {
    $http.post('/HRMspj/PjAdd', Object.assign({}, vm.para, vm.data)).success((d) => {
      if (d.success) {
        alert('');
        location.reload();
      } else {
        alert('');
      }
    });
  };
  vm.close = function () {
    console.log(1);
    vm.isshow = false;
  };
}



