export default function ZwscCtrl($http) {
  var vm = this;
  vm.list = [
     { name: 'logo', key: 'logo', img: true, width: '20%', rczp: true },
     { name: '', key: 'compay', width: '10%' },
     { name: '', key: 'position', width: '20%' },
     { name: '', key: 'city', width: '10%' },
     { name: '', key: 'gznx', width: '10%' },
     { name: '', key: 'xueli', width: '10%' },
     { name: '', key: 'gzdz', width: '10%' },
  ];
     // --
  vm.cancel = function (cancelId, canceltype) {
    $http.post('/HRSczw/Qxsc', { jobid: cancelId, type: canceltype }).success(res => {
      if (res.success) {
        location.reload();
      }
    });
  };
}



